.App {
  text-align: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #232526 0%, #414345 100%);
  font-family: 'Inter', 'Segoe UI', Arial, sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background: rgba(34, 40, 49, 0.95);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-top: 60px;
  font-size: 1.2rem;
  color: #f5f6fa;
}

h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 32px;
  letter-spacing: 1px;
  color: #00e1ff;
  text-shadow: 0 2px 16px #00e1ff33;
}

.wallet-card {
  background: #23272f;
  border-radius: 18px;
  box-shadow: 0 4px 32px #00000033;
  padding: 32px 28px 24px 28px;
  margin-top: 32px;
  min-width: 320px;
  max-width: 90vw;
  display: inline-block;
  box-sizing: border-box;
}

.balances-section {
  margin-top: 32px;
  background: #181c22;
  border-radius: 14px;
  padding: 24px 18px;
  box-shadow: 0 2px 12px #00e1ff22;
  display: inline-block;
  min-width: 260px;
  box-sizing: border-box;
}

.balances-section h3 {
  color: #00e1ff;
  margin-bottom: 18px;
  font-size: 1.3rem;
}

.balance-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
  font-size: 1.1rem;
  padding: 6px 0;
  border-bottom: 1px solid #23272f;
}

.balance-row:last-child {
  border-bottom: none;
}

.token-symbol {
  font-weight: 600;
  color: #00e1ff;
}

.token-balance {
  font-family: 'Fira Mono', monospace;
  color: #f5f6fa;
}

/* Enhanced ConnectButton styles */
.ConnectButton {
  background: linear-gradient(90deg, #7b2ff2 0%, #f357a8 100%);
  color: #fff;
  font-weight: 700;
  font-size: 1.15rem;
  border: none;
  border-radius: 14px;
  padding: 16px 38px;
  margin-bottom: 24px;
  box-shadow: 0 4px 24px #f357a855;
  cursor: pointer;
  transition: background 0.18s, box-shadow 0.18s, transform 0.18s;
  outline: none;
  letter-spacing: 0.5px;
  position: relative;
  z-index: 2;
  display: inline-block;
}
.ConnectButton:hover {
  background: linear-gradient(90deg, #f357a8 0%, #7b2ff2 100%);
  box-shadow: 0 8px 32px #f357a888;
  transform: translateY(-2px) scale(1.04);
}
.ConnectButton:active {
  background: linear-gradient(90deg, #7b2ff2 0%, #f357a8 100%);
  box-shadow: 0 2px 8px #f357a855;
  transform: scale(0.98);
}

/* RainbowKit wallet modal enhancements */
/* Wallet Connect Modal Customization */
.rk-modal, .rk-dialog-content {
  background: #181c22 !important;
  border-radius: 20px !important;
  box-shadow: 0 8px 40px #00e1ff33 !important;
  padding: 32px 18px !important;
  max-width: 420px !important;
  width: 95vw !important;
  margin: 0 auto !important;
  top: 50% !important;
  left: 50% !important;
  transform: translate(-50%, -50%) !important;
  position: fixed !important;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  animation: fadeInModal 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes fadeInModal {
  from { opacity: 0; transform: translateY(30px) scale(0.98); }
  to { opacity: 1; transform: translateY(0) scale(1); }
}

.rk-modal h1, .rk-dialog-content h1, .rk-dialog-content__title, .rk-modal__title {
  color: #00e1ff !important;
  font-size: 2rem !important;
  font-weight: 700 !important;
  margin-bottom: 18px !important;
  letter-spacing: 0.5px;
  text-align: center;
  text-shadow: 0 2px 16px #00e1ff33;
}

.rk-modal .rk-divider, .rk-dialog-content .rk-divider {
  height: 2px;
  background: linear-gradient(90deg, #00e1ff 0%, #0072ff 100%);
  border: none;
  margin: 18px 0 24px 0;
  opacity: 0.5;
}

.rk-wallet-options-list {
  display: grid !important;
  grid-template-columns: 1fr 1fr;
  gap: 18px;
  width: 100%;
  margin-bottom: 12px;
  box-sizing: border-box;
  justify-content: center !important;
  align-items: center !important;
}

@media (max-width: 600px) {
  .rk-wallet-options-list {
    grid-template-columns: 1fr !important;
    gap: 12px;
    justify-content: center !important;
    align-items: center !important;
  }
}

.rk-wallet-option {
  background: #23272f !important;
  border-radius: 14px !important;
  margin: 0 !important;
  padding: 20px 22px !important;
  transition: background 0.2s, box-shadow 0.2s, transform 0.18s;
  box-shadow: 0 2px 12px #00e1ff22 !important;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 18px;
  min-width: 0;
  min-height: 80px;
  justify-content: flex-start;
  box-sizing: border-box;
  margin-left: auto !important;
  margin-right: auto !important;
}
.rk-wallet-option:hover {
  background: #181c22 !important;
  box-shadow: 0 4px 24px #00e1ff44 !important;
  transform: scale(1.045);
  z-index: 2;
}
.rk-wallet-option .rk-wallet-option-icon {
  transition: transform 0.18s;
  min-width: 48px;
  min-height: 48px;
  max-width: 48px;
  max-height: 48px;
  border-radius: 8px;
  background: #23272f;
  display: flex;
  align-items: center;
  justify-content: center;
}
.rk-wallet-option:hover .rk-wallet-option-icon {
  transform: scale(1.15) rotate(-6deg);
}
.rk-wallet-option .rk-wallet-option-name {
  color: #00e1ff !important;
  font-weight: 600 !important;
  font-size: 1.15rem !important;
}
.rk-wallet-option .rk-wallet-option-description {
  color: #b2becd !important;
  font-size: 1rem !important;
}

/* RainbowKit modal overlay */
.rk-modal-overlay {
  background: rgba(0, 225, 255, 0.08) !important;
  backdrop-filter: blur(4px) !important;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Enhanced Disconnect Wallet button styles for RainbowKit */
.rk-account-button__disconnect,
.rk-disconnect-button,
.rk-account-modal__disconnect-button {
  background: linear-gradient(90deg, #ff3b3b 0%, #ff7b47 100%) !important;
  color: #fff !important;
  font-weight: 700 !important;
  font-size: 1.08rem !important;
  border: none !important;
  border-radius: 10px !important;
  padding: 13px 28px !important;
  margin: 18px 0 0 0 !important;
  box-shadow: 0 2px 16px #ff3b3b33 !important;
  cursor: pointer !important;
  transition: background 0.18s, box-shadow 0.18s, transform 0.18s !important;
  letter-spacing: 0.5px;
  outline: none !important;
  display: block;
  width: 100%;
  max-width: 320px;
}
.rk-account-button__disconnect:hover,
.rk-disconnect-button:hover,
.rk-account-modal__disconnect-button:hover {
  background: linear-gradient(90deg, #ff7b47 0%, #ff3b3b 100%) !important;
  box-shadow: 0 4px 24px #ff3b3b66 !important;
  transform: translateY(-2px) scale(1.04);
}

@media (max-width: 600px) {
  .rk-account-button__disconnect,
  .rk-disconnect-button,
  .rk-account-modal__disconnect-button {
    padding: 12px 0 !important;
    font-size: 1rem !important;
    max-width: 100vw;
  }
}

/* Remove/hide extra instructional text and images in the wallet connect modal */
.rk-modal__desc, .rk-dialog-content__desc, .rk-modal__illustration, .rk-dialog-content__illustration, .rk-modal__footer, .rk-dialog-content__footer, .rk-modal__learn-more, .rk-dialog-content__learn-more, .rk-modal__help, .rk-dialog-content__help, .rk-modal__info, .rk-dialog-content__info, .rk-modal__content > div[style*='What is a Wallet'], .rk-modal__content > div[style*='A Home for your Digital Assets'], .rk-modal__content img, .rk-dialog-content img {
  display: none !important;
  height: 0 !important;
  margin: 0 !important;
  padding: 0 !important;
  background: none !important;
  border: none !important;
  visibility: hidden !important;
}
